.header__menu-mobile {
  display: none;
}

.button {
  border: 1px solid #444444;
  background-color: #ffffff;
  height: 50px;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  font-family: "Open Sans";
  box-sizing: border-box;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
  transition: 0.5s;
}
.button:active {
  background-color: #444444;
  color: #7c7c7c;
  transition: 0.1s;
}

.section-title {
  display: inline;
  font-weight: bold;
  font-size: 53px;
  color: #000000;
  font-family: "Open Sans";
}

.site-list__item--catalog {
  list-style-type: none;
  height: 70px;
  display: flex;
  align-items: center;
  box-shadow: inset -1px 0 0 0 #e7e7e7;
}

/*Шапка*/
.header__wrapper {
  max-width: 1050px;
  margin: 0 auto;
}

.border-wrap {
  box-sizing: border-box;
  max-width: 1150px;
  margin: 0 auto;
  border-bottom: 1px solid #e7e7e7;
}

.page-header {
  background-color: #ffffff;
  font-size: 19px;
  font-weight: bold;
  font-family: "Open Sans";
}
.page-header a {
  text-decoration: none;
  color: #000000;
}

.nav__list {
  padding-left: 0%;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.site-list__item {
  list-style-type: none;
  height: 70px;
  display: flex;
  align-items: center;
}
.site-list__item--index {
  list-style-type: none;
  height: 70px;
  display: flex;
  align-items: center;
}
.site-list__item--basket {
  list-style-type: none;
  height: 70px;
  display: flex;
  align-items: center;
  padding-right: 45px;
  padding-left: 45px;
  display: flex;
  align-items: center;
  box-shadow: inset 1px 0 0 0 #e7e7e7;
}
.site-list__item--basket::after {
  content: "Корзина: пока пуста";
}
.site-list__item--search {
  list-style-type: none;
  height: 70px;
  display: flex;
  align-items: center;
  padding-right: 10px;
  padding-left: 45px;
  align-items: center;
  box-shadow: inset 1px 0 0 0 #e7e7e7;
}
.site-list__item--catalog {
  padding-right: 20px;
}

.nav__item {
  text-decoration: none;
  color: #000000;
}
.nav__item:hover {
  opacity: 0.66;
  transition: 0.9s;
}
.nav__item:active {
  opacity: 0.3;
}
.nav__item--basket {
  list-style-type: none;
  background-image: url("../img/cart.svg.png");
  background-repeat: no-repeat;
  width: 21px;
  height: 18px;
}
.nav__item--basket:hover {
  background-image: url("../img/cart-hover.png");
  transition: 0.9s;
}
.nav__item--basket:active {
  opacity: 0.3;
}
.nav__item--search {
  list-style-type: none;
  background-repeat: no-repeat;
  background-image: url("../img/search.svg.png");
  width: 21px;
  height: 18px;
}
.nav__item--search:hover {
  background-image: url("../img/search-hover.png");
  transition: 0.9s;
}
.nav__item--search:active {
  opacity: 0.3;
}
.nav__item--index {
  width: 149px;
  height: 72px;
  background-image: url("../img/mishka-logo-desktop.png");
  background-repeat: no-repeat;
  margin-left: 19px;
}
.nav__item--index:hover {
  opacity: 0.6;
  transition: 0.5s;
}
.nav__item--index:active {
  opacity: 0.3;
}

.burger {
  display: none;
}

.burger--open {
  display: none;
}

@media (max-width: 768px) {
  .page-header {
    width: 100%;
    margin: 0;
  }

  .border-wrap {
    width: 87%;
    padding-left: 6.5%;
    padding-right: 6.5%;
    margin: 0%;
  }

  .header__wrapper {
    width: 87%;
    margin: 0;
  }

  .burger {
    display: none;
  }

  .burger--open {
    display: none;
  }

  .nav {
    width: 87%;
    margin: 0;
  }

  .nav__list {
    display: flex;
    justify-content: space-between;
    width: 80%;
  }

  .site-list__item--catalog {
    display: none;
  }
  .site-list__item--index {
    width: 42%;
  }
  .site-list__item--search {
    width: 24%;
    padding-left: 10%;
  }
  .site-list__item--basket {
    padding-left: 10%;
  }

  .hidden {
    display: none;
  }

  .nav__item--index {
    background-image: url("../img/mishka-logo-tablet.png");
    height: 46px;
    align-items: center;
    margin-left: 0;
  }
  .nav__item--basket {
    display: flex;
    justify-content: space-between;
  }

  .section-title {
    font-size: 43px;
  }
}
@media (max-width: 320px) {
  .page-header {
    width: 100%;
  }

  .border-wrap {
    width: 100%;
  }

  .header__wrapper {
    width: 100%;
  }

  .nav {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    min-height: 50px;
    align-items: center;
  }
  .nav--closed {
    display: none;
  }

  .burger {
    display: flex;
    background-image: url("../img/mobile/burger.png");
    width: 20px;
    height: 14px;
    background-repeat: no-repeat;
    border: none;
    background-color: #ffffff;
    align-items: center;
  }
  .burger:hover {
    cursor: pointer;
  }
  .burger--open {
    display: flex;
  }

  .burger--close {
    display: flex;
    background-image: url("../img/mobile/menu-close.png");
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    border: none;
    background-color: #ffffff;
    position: absolute;
    left: 76%;
    top: 5.5%;
  }

  .nav__list {
    flex-direction: column;
    text-align: center;
    width: 100%;
    align-items: center;
  }

  .site-list__item {
    font-size: 17px;
    display: flex;
    width: 100%;
    justify-content: center;
    border-bottom: 1px solid #e7e7e7;
  }
  .site-list__item--index {
    order: -1;
    border-bottom: 1px solid #e7e7e7;
    width: 100%;
    justify-content: center;
  }
  .site-list__item--catalog {
    width: 100%;
    justify-content: center;
    font-size: 17px;
    display: flex;
    box-shadow: none;
    padding: 0;
    border-bottom: 1px solid #e7e7e7;
  }
  .site-list__item--search {
    box-shadow: none;
    display: flex;
    width: 100%;
    padding: 0;
    justify-content: center;
    border-bottom: 1px solid #e7e7e7;
  }
  .site-list__item--search::after {
    content: "Поиск по сайту";
    font-size: 17px;
  }
  .site-list__item--basket {
    box-shadow: none;
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 0;
  }
  .site-list__item--basket::after {
    display: contents;
    content: "Корзина: пока пуста";
    font-size: 17px;
  }

  .nav__item--index {
    width: 86px;
    display: flex;
    background-image: url("../img/mobile/mishka-logo-mobile.png");
  }
  .nav__item--basket {
    width: 28px;
  }
  .nav__item--test {
    box-sizing: border-box;
    background-image: url("../img/mobile/mishka-logo-mobile.png");
    background-size: contain;
    background-repeat: no-repeat;
    width: 73px;
    height: 35px;
    margin-right: 30%;
  }

  .not-seen {
    display: none;
  }
}
