/* Заглавный блок*/
.hero__wrapper {
  max-width: 1050px;
  margin: 0 auto;
  box-sizing: border-box;
}

.hero {
  position: relative;
  background-color: #f9f9f9;
  height: 90vh;
  background-image: url("../img/hero-desktop.jpg");
  background-size: auto;
  background-position: center center;
  background-repeat: no-repeat;
}

.hero-nav__list {
  padding-left: 0%;
  display: flex;
  align-items: center;
  max-width: 1050px;
}

.site-list__item--bottom {
  font-size: 15px;
  margin-top: 10px;
  font-weight: 700;
  list-style-type: none;
  padding-right: 50px;
  font-family: "Open Sans";
}
.site-list__item--bottom-delivery {
  font-size: 15px;
  margin-top: 10px;
  font-weight: normal;
  list-style-type: none;
  color: #444444;
  padding-left: 428px;
  font-family: "Open Sans";
}

.hero__title {
  padding-top: 70px;
  font-size: 53px;
  font-family: "Open Sans";
  text-align: center;
}

.hero__list {
  box-sizing: border-box;
  position: relative;
  padding-left: 0;
  bottom: -399px;
  display: flex;
  list-style: none;
}
.hero__list::after {
  position: absolute;
  bottom: -10%;
  content: url("../img/zigzag-fill-desktop.svg.png");
  overflow-x: hidden;
  max-width: 100%;
}

.hero__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 20px;
  font-family: "Open Sans";
  text-align: center;
}

.hero__item--toys, .hero__item--interior {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  padding: 33px;
}

.hero__item--interior {
  background-color: #63d1bb;
}
.hero__item--interior::after {
  content: url("../img/interior.svg.png");
}
.hero__item--interior:active::after {
  opacity: 0.3;
  transition: 0.3s;
}

.hero__item--toys {
  background-color: #56c3b2;
}
.hero__item--toys::after {
  content: url("../img/toy-svg.png");
}
.hero__item--toys:active::after {
  opacity: 0.3;
  transition: 0.3s;
}

.hero__link:hover {
  transform: translateY(-7px);
  transition: 0.5s;
}
.hero__link:active .hero__link--text {
  opacity: 0.3;
  transition: 0.3s;
}

@media (max-width: 768px) {
  .hero {
    width: 100%;
  }

  .hero__wrapper {
    max-width: 100%;
  }

  .hero__title {
    font-size: 43px;
  }

  .hero-nav__list {
    max-width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }

  .hero__list {
    max-width: 100%;
    bottom: -312px;
    top: 312px;
  }
  .hero__list::after {
    max-width: 100%;
  }

  .hero__item--interior {
    width: 50%;
    padding: 4.2%;
  }
  .hero__item--toys {
    width: 50%;
  }

  .site-list__item--bottom {
    margin-right: 10px;
    padding-right: 0;
  }
  .site-list__item--bottom-delivery {
    padding-left: 0px;
    padding-right: 0;
  }
}
@media (max-width: 320px) {
  .hero {
    background-size: cover;
    height: auto;
    width: 100%;
  }

  .hero__title {
    font-size: 27px;
    line-height: 30px;
    margin-top: 0px;
    padding-top: 110px;
  }

  .hero-nav__list {
    display: none;
  }

  .hero__list {
    display: block;
    max-width: 320px;
    top: 120px;
  }
  .hero__list::after {
    display: none;
  }

  .hero__item--interior {
    width: 100%;
  }
  .hero__item--toys {
    width: 100%;
  }

  .hero__link {
    max-height: 103px;
  }
}
