/*Товар недели*/
.week-product {
  margin-top: 100px;
}

.week-product__wrapper {
  max-width: 1050px;
  margin: 0 auto;
  position: relative;
}
.week-product__wrapper::after {
  content: url("../img/zigzag-line-desktop.svg.png");
  position: absolute;
  left: 0;
  max-width: 100%;
  bottom: -110px;
}

.section-title-main {
  display: inline-flex;
  align-items: center;
  z-index: 1;
}

.week-product__small-wrap {
  max-width: 875px;
  margin-left: 175px;
  box-sizing: border-box;
}

.section-title--week-prod {
  display: inline;
  box-sizing: border-box;
  min-width: 175px;
  text-align: center;
  text-transform: uppercase;
  font-family: "Open Sans";
  font-size: 17px;
  line-height: 16px;
  color: #63d1bb;
  vertical-align: top;
  padding: 21px;
  z-index: 1;
}

.week-product__info {
  max-width: 700px;
  display: inline-block;
  position: relative;
  font-family: "Open Sans";
  box-sizing: border-box;
}

.week-product__name {
  display: inline;
  position: inherit;
  font-size: 53px;
  font-family: "Open Sans";
  font-weight: bold;
  z-index: 1;
  position: relative;
}

.week-product__description {
  font-family: "Open Sans";
  max-width: 300px;
  font-size: 17px;
  line-height: 30px;
  color: #444444;
  margin-top: 60px;
  margin-bottom: 60px;
}

.week-product__char {
  line-height: 39px;
  max-width: 300px;
  border-bottom: 1px solid #e7e7e7;
}

.week-product__desc {
  display: inline-block;
  width: 98px;
}

.week-product__price {
  margin-top: 110px;
  color: #444444;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  width: 300px;
}

.week-product__button {
  margin-top: 45px;
  width: 300px;
}

.week-product__img {
  position: absolute;
  left: 116%;
  z-index: 0;
  top: -12%;
}

@media (max-width: 768px) {
  .week-product {
    width: 100%;
  }

  .week-product__wrapper {
    width: 87%;
  }
  .week-product__wrapper::after {
    max-width: 87%;
    overflow-x: hidden;
  }

  .week-product__small-wrap {
    max-width: 87%;
    margin-left: 0px;
  }

  .week-product__name {
    font-size: 43px;
    order: 1;
  }

  .section-title--week-prod {
    order: 2;
    max-width: 22.8%;
    min-width: 10px;
    padding: 2.7%;
